<template>
  <!-- <div class="filter-item" style="background-image:url(${업로드이미지})"> </div> -->
  <div v-on:click="filterClick()" :class="'filter-item' + 필터" :style="'background-image:url(${업로드이미지})'">
    <slot></slot>  
  </div>
</template>

<script>
import EventBus from './../EventBus.js';

export default {
  props: {
    업로드이미지 : String,
    필터 : String,
  },
  methods:{
    filterClick(){
      EventBus.$emit('전송', this.필터)  //전송이 function명 -App.vue로 전송
    }
  },
}
</script>

<style>
.filter-item {
width: 100px;
height: 100px;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
background-position : center;
}

</style>