<template>
  <div class="body">

      <!-- <button v-on:click="$emit('이름전송', name)">버튼</button> -->

    <!-- <Post v-bind:포스팅들="포스팅들[0]" />
    <Post v-bind:포스팅들="포스팅들[1]" />
    <Post v-bind:포스팅들="포스팅들[2]" />-->
    <div v-if="step === 0">
      <Post v-for="(작명,i) in 포스팅들" v-bind:포스팅들="포스팅들[i]" :key="i" />
    </div>

    <div v-if="step === 1">
      <div :class="['upload-image'+ 클릭한필터명]" :style="`background-image:url(${업로드이미지})`"></div>
      <div class="filters">
        <!-- <Filterbox :업로드이미지="업로드이미지"/> -->
        <!-- <FilterBox v-for="(작명,i) in 필터들" :key="i" :업로드이미지="업로드이미지" /> -->
        
        <FilterBox v-for="(작명,i) in 필터들" 
        :필터="필터들[i]" 
        :key="i" 
        :업로드이미지="업로드이미지">
          <!-- 아까 slot자리에 들어갈 html을 입력 -->
          <p>{{작명}}</p>
        </FilterBox>

      </div>
    </div>

    <div v-if="step === 2">
      <div class="upload-image" :style=" `background-image:url(${업로드이미지})` "></div>
      <div class="write">
        <textarea v-on:input="$emit('글입력', $event.target.value)" class="write-box">write!</textarea>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from './FilterBox.vue';

export default {
  name: "body",
  data() {
    return {
      name: "John",
      필터들 : ["normal", "clarendon", "gingham", "moon", "lark", "reyes", "juno", "slumber", "aden", "perpetua", "mayfair", "rise", "hudson", "valencia", "xpro2", "willow", "lofi", "inkwell", "nashville"],
    };
  },
  components: {
    Post,
    FilterBox
  },
  props: {
    포스팅들: Array,
    step: Number,
    업로드이미지: String,
    클릭한필터명: String,
  }
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>