import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios';

Vue.use(Vuex);

export let store = new Vuex.Store({
  state: {
      name : 'JS',
      age : 25
  },
  getters : {
      //데이터 꺼내는 법
      GET_NAME(state){
          return state.name+'님';
      },
      GET_AGE(state){
          return state.age;
      }
  },
  mutations: {
      //데이터 수정하는 법
      SET_NAME(state, payLoad){  //payLoad : 자료
        state.name = payLoad;
      },
      SET_AGE(state){
        state.age++;
      },
      더보기수정(state,자료){
        state.포스팅들.unshift(자료);
    }
  },
  actions : {
      //ajax요청을 위해 사용
      더보기(context){
        //ajax요청을 해서 받아온 결과를 
        Axios.get('url').then((결과)=>{
            context.commit('더보기수정', 결과.data);
        });
    }
  }  
})
